@import url(https://fonts.googleapis.com/css2?family=Catamaran&family=Baloo+Tamma+2&display=swap);
.App {
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.loader {
    margin: auto;
    padding: 100px 250px;
}

@media only screen and (max-width: 600px) {
    .loader {
        margin: auto;
        padding: 100px 50px;
    }
}
.credits-ribbon {
    top: 0px;
    left: 0px;
    position: fixed;
    background: #1a181888;
    transform: rotate(320deg);
    padding: 10px;
    margin-left: -50px;
    margin-top: 50px;
    width: 265px;
    color: #f0f0f0;
    text-align: center;
}
.story {
    padding-top: 10px;
    margin-bottom: 20px;
    border-top: 1px solid #cccccc;
    font-family: 'Catamaran', sans-serif;
}

.story:first-of-type {
    border-top: 0;
    margin-top: 50px;
}

.story:last-of-type {
    margin-bottom: 0;
    padding-bottom: 0;
}

.storyTitle {
    margin: 0;
    margin-bottom: 5px;
    font-size: 18px;
    line-height: 1.8;
    text-decoration: none;
}

.storyTitle a {
    color: #2e2e2c;
    background-color: #f8dc3d;
    text-decoration: none;
}

.storyMeta {
    font-style: italic;
    margin-left: 10px;
    font-family: 'Baloo+Tamma', sans-serif;
}

.storyMeta span {
    font-weight: bold;
}
:root {
    --primary-background-color: #fafafa;
    --primary-color: #202020;
    --primary-font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    --primary-font-size: 1.2rem;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    line-height: 1;
    color: #202020;
    color: var(--primary-color);
    background-color: #fafafa;
    background-color: var(--primary-background-color);
    font-family: Arial, Helvetica, sans-serif;
    font-size: 16px;
    margin-left: 30px;
}
